import React, { useEffect } from "react";
import { Link } from "react-router-dom";

import { useGlobalContext } from "../../context";

import classes from "./Home.module.css";

import img from "../../assets/trash_cans.png";
import contact from "../../assets/contact_button.svg";

const Home = () => {
  const { setPath } = useGlobalContext();

  useEffect(() => {
    setPath("home");
    // eslint-disable-next-line
  }, []);

  return (
    <React.Fragment>
      <div className={classes.Home}>
        <div className={classes.container}>
          <div className={classes.imgContainer}>
            <img
              src={img}
              alt="four trash cans with dorun written on them"
              className={classes.image}
            />
            <div className={classes.headingContainer}>
              <h1>We Bring</h1>
              <h2>Change</h2>
            </div>
            <p className={classes.text}>
              Plastic cannot be completely avoided. Recycling is the need of the
              hour. At Dorun, we promote recycling and ensure that our plastic
              products are recyclable.
            </p>
            <Link to="/contact">
              <img
                src={contact}
                alt="navigate to contact page"
                className={classes.contactUsMobile}
              />
            </Link>
            <p className={classes.MobileDesigner}>
              Designed by{" "}
              <a
                href="https://helloantony.in/"
                target="_blank"
                rel="noreferrer"
              >
                Antony Chiramel
              </a>
            </p>
          </div>
          <div className={classes.grey}></div>
          <div className={classes.PageNo}>
            <h1>01</h1>
          </div>
          <div className={classes.title}>
            <h1>We bring</h1>
            <h2>Change</h2>
            <p className={classes.para}>
              Plastic cannot be completely avoided. Recycling is the need of the
              hour. At Dorun, we promote recycling and ensure that our plastic
              products are recyclable.
            </p>
            <Link to="/contact">
              <img
                src={contact}
                alt="navigate to contact page"
                className={classes.contactUs}
              />
            </Link>
            <p className={classes.designer}>
              Designed by{" "}
              <a
                href="https://helloantony.in/"
                target="_blank"
                rel="noreferrer"
              >
                Antony Chiramel
              </a>
            </p>
          </div>
          <div className={classes.circleOne}></div>
          <div className={classes.circleTwo}></div>
          <div className={classes.circleThree}></div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Home;
